import React, { useState, useRef, useEffect } from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";
import "./AppHeader.css";
import logo from "../../img/woodec-logo.svg";
import logoTablet from "../../img/logoMobile.svg";
import iconMobileMenu from "../../img/menuIcon.svg";
import iconMobileMenuClose from "../../img/close_icon.svg";
import IconLang from "../../img/arrow-down-3101.svg";

const AppHeader = (props) => {
  const lang = props.lang;
  const data = props.data;
  const setLang = props.setLang;
  const langs = props.langs;
  const setActiveMenuItem = props.setActiveMenuItem;
  const activeMenuItem = props.activeMenuItem;
  const setExpandedAccordion = props.setExpandedAccordion;
  const [langDrop, setLangDrop] = useState(false);
  const [mobileMenu, setMobileMenu] = useState(false);
  const blockRef = useRef(null);
  //const [anchorElNav, setAnchorElNav] = React.useState(null);
  //const [anchorElUser, setAnchorElUser] = React.useState(null);
  const handleChange = (e) => {
    setLang(e.target.value);
    handleLangMenu();
  };
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (blockRef.currrent.contains(event.target)) {
        setMobileMenu(!mobileMenu);
      }
    };
  });
  const handleMobileMenu = () => {
    setMobileMenu(!mobileMenu);
  };
  const handleLangMenu = () => {
    setLangDrop(!langDrop);
  };
  const screenWidth = window.innerWidth;

  // const handleOpenNavMenu = (event) => {
  //   setAnchorElNav(event.currentTarget);
  // };
  // const handleOpenUserMenu = (event) => {
  //   setAnchorElUser(event.currentTarget);
  // };
  //
  // const handleCloseNavMenu = () => {
  //   setAnchorElNav(null);
  // };

  //const handleCloseUserMenu = () => {
  //  setAnchorElUser(null);
  //};

  return (
    <>
      <header className="containerNavbar">
        <div className="containerLogo">
          <Link to={`/`} onClick={() => setActiveMenuItem("/")}>
            <img src={logo} className="logo" alt="WOODEC" />
          </Link>
        </div>
        <div className="spacer"></div>
        <nav className="containerMenu">
          {Object.keys(data).map((key) => {
            return (
              <Link
                to={`/${key}`}
                onClick={() => {
                  setActiveMenuItem(`${key}`);
                  setExpandedAccordion("history");
                }}
                key={`menu-${key}`}
              >
                <li
                  className={`nav-link ${
                    key === activeMenuItem ? "active" : ""
                  }`}
                >
                  {data[key]}
                </li>
              </Link>
            );
          })}
        </nav>
        <div className="spacer"></div>
        <nav className="dropdown">
          <button className="dropbtn">
            {lang}
            <img src={IconLang} className="iconLang" alt="" />
          </button>

          <div className="dropdown-content">
            {langs.map((langMenu) => {
              if (langMenu !== lang) {
                return (
                  <button
                    onClick={handleChange}
                    value={langMenu}
                    key={`lang-${langMenu}`}
                  >
                    {langMenu}
                  </button>
                );
              } else {
                return "";
              }
            })}
          </div>
        </nav>
      </header>
      <header className="containerNavbarTablet">
        <div className="containerLogoTablet">
          <Link to={`/`} onClick={() => setActiveMenuItem("/")}>
            <img className="logoTablet" src={logoTablet} alt="" />
          </Link>
        </div>
        <div className="spacerTablet"></div>
        <div className="menuMobileContainer dropdown">
          <button className="dropbtn" onClick={handleLangMenu}>
            {lang}
            <img src={IconLang} className="iconLang" alt="" />
          </button>

          <motion.div
            className="dropdown-contentMenu lang"
            style={{ width: `${screenWidth}px` }}
            initial={{ height: 0 }}
            animate={{
              height: langDrop ? "auto" : 0,
            }}
            transition={{ duration: 0.7, ease: "easeInOut" }}
          >
            {langs.map((langMenu) => {
              if (langMenu !== lang) {
                return (
                  <button
                    onClick={handleChange}
                    value={langMenu}
                    key={`lang-${langMenu}`}
                  >
                    {langMenu}
                  </button>
                );
              } else {
                return "";
              }
            })}
          </motion.div>
        </div>
        <div className="menuMobileContainer dropdownMenu">
          <button
            className="menuButtonMobile dropbtn"
            onClick={handleMobileMenu}
          >
            <img
              className={`menuIconMobile ${mobileMenu ? "close" : ""}`}
              src={iconMobileMenu}
            />
            <img
              className={`menuIconMobile ${mobileMenu ? "" : "close"}`}
              src={iconMobileMenuClose}
            />
          </button>
          <motion.div
            className="dropdown-contentMenu"
            style={{ width: `${screenWidth}px` }}
            initial={{ height: 0 }}
            animate={{
              height: mobileMenu ? "auto" : 0,
            }}
            transition={{ duration: 0.7, ease: "easeInOut" }}
          >
            {Object.keys(data).map((key) => {
              return (
                <Link
                  to={`/${key}`}
                  onClick={() => {
                    setActiveMenuItem(`${key}`);
                    handleMobileMenu();
                    setExpandedAccordion("history");
                  }}
                  key={`menu-${key}`}
                >
                  <button
                    className={`nav-link ${
                      key === activeMenuItem ? "active" : ""
                    }`}
                  >
                    {data[key]}
                  </button>
                </Link>
              );
            })}
          </motion.div>
        </div>
      </header>
    </>
  );
};
export default AppHeader;
