import React, { useState } from "react";
import "./Card.css";

const Card = (props) => {
  const el = props.el;
  const [rotate, setRotate] = useState("");
  return (
    <div className="scene">
      <div
        className={`card ${rotate}`}
        onClick={() =>
          rotate === "" ? setRotate("is-flipped") : setRotate("")
        }
      >
        <img
          className="card-img-top face front"
          src={el.img}
          alt={el.position}
        />
        <div className="card-body face back">
          <h5 className="card-title">
            {el.firstName} {el.lastName}
          </h5>
          <h5 className="card-title">{el.position}</h5>
          <p className="card-text">{el.desc}</p>
        </div>
      </div>
    </div>
  );
};
export default Card;
