import React, { useState, useEffect } from "react";
import { ReactComponent as Twitter } from "../../img/twitter.svg";
import { ReactComponent as Facebook } from "../../img/facebook.svg";
import { ReactComponent as Youtube } from "../../img/youtube.svg";
import { ReactComponent as Instagram } from "../../img/instagram.svg";
import { ReactComponent as Phone } from "../../img/phone.svg";
import maskGroup from "../../img/maskGroup.svg";
import "./Social.css";

const Social = () => {
  const [hasScrolled, setHasScrolled] = useState(false);
  let scroll = 0;
  useEffect(() => {
    // Функция для проверки скролла и обновления состояния
    const checkScroll = () => {
      if (window.scrollY !== scroll) {
        scroll = window.scrollY;
        setHasScrolled(true);
      } else {
        setHasScrolled(false);
      }
    };

    // Запускаем функцию checkScroll при монтировании компонента
    checkScroll();

    // Устанавливаем интервал для периодической проверки скролла
    const scrollInterval = setInterval(checkScroll, 100); // Проверка каждые 100 миллисекунд

    return () => {
      // Убираем интервал и слушателя при размонтировании компонента
      clearInterval(scrollInterval);
    };
  }, []);
  return (
    <div
      className={`socialContainer ${hasScrolled ? "animateOut" : "animateIn"}`}
    >
      {/*hasScrolled ? (
        <p>Страница была прокручена</p>
      ) : (
        <p>Страница не была прокручена</p>
      )*/}
      <div className="socialButton">
        <button className="socialIcon">
          <Facebook className="svgSocial" />
        </button>
        <button className="socialIcon">
          <Twitter className="svgSocial" />
        </button>
        <button className="socialIcon">
          <Instagram className="svgSocial" />
        </button>
        <button className="socialIcon">
          <Youtube className="svgSocial" />
        </button>
        <button className="socialIcon">
          <Phone className="svgSocial" />
        </button>
      </div>
      <img className="socialMask" src={maskGroup} alt="" />
    </div>
  );
};
export default Social;
