import React from "react";
import { Link } from "react-router-dom";
import "./AboutUsHome.css";
import mark from "../../img/maskGroup.svg";

const AboutUsHome = (props) => {
  const data = props.data;
  const activeMenuItem = props.activeMenuItem;
  const setActiveMenuItem = props.setActiveMenuItem;
  const expandedAccordion = props.expandedAccordion;
  const setExpandedAccordion = props.setExpandedAccordion;

  return (
    <>
      <div className="aboutUsEmpty"></div>
      <section className="aboutUsMainContainer">
        <div
          className="aboutUsMainImageContainer"
          style={{ background: `no-repeat url("${data.aboutUsPhotoUrl}")` }}
        >
          <img className="aboutUsMainImageMark" src={mark} alt="" />
        </div>
        <div className="aboutUsMainTextContainer">
          <h2 className="aboutUsMainTitle">{data.aboutUsTitle}</h2>
          <p className="aboutUsMainText">{data.aboutUsDesc}</p>
          <button className="aboutUsMainButton">{data.aboutUsButton}</button>
        </div>
      </section>
      <div className="aboutUsEmpty"></div>
      <section className="aboutUsIconsContainer">
        {data.aboutUsIcons.map((el) => (
          <Link
            to={`/aboutUs`}
            onClick={() => {
              setActiveMenuItem("aboutUs");
              setExpandedAccordion(`${el.name}`);
            }}
            className="aboutUsIconItem"
          >
            <div className="">
              <img src={el.url} className="aboutUsIconImg" alt={el.name} />

              <p className="aboutUsIconText">{el.text}</p>
            </div>
          </Link>
        ))}
      </section>
      <div className="aboutUsEmpty"></div>
    </>
  );
};
export default AboutUsHome;
