import React, { useState } from "react";
import { motion } from "framer-motion";

import "./Accordion.css";
import Card from "../Card";

const AccordionAbout = (props) => {
  const data = props.data;
  const [expandedTeam, setExpandedTeam] = useState("panel1");
  const setExpandedAccordion = props.setExpandedAccordion;
  const expandedAccordion = props.expandedAccordion;
  if (expandedAccordion === "") {
    setExpandedAccordion(data.accordion[0].section);
  }

  const handleClick = (panel) => {
    if (expandedAccordion !== panel) {
      setExpandedAccordion(panel);
    }
  };
  const handleChangeTeam = (panel) => {
    console.log("Panel before", panel);
    if (expandedTeam !== panel) {
      setExpandedTeam(panel);
      console.log("Panel if", expandedTeam);
    }
    console.log("Panel after", expandedTeam);
  };

  return (
    <>
      {data.accordion.map((section, index) => (
        <div
          className="accordionSection"
          onClick={() => handleClick(section.section)}
          key={`section-${index + 1}`}
        >
          <motion.header
            className="accordionHeaderContainer"
            initial={{ background: "#cc9753" }}
            animate={{
              background:
                expandedAccordion === section.section ? "#083063" : "#cc9753",
            }}
            transition={{ duration: 0.7, ease: "easeInOut" }}
          >
            <div className="accordionHeader" key={`header-${index + 1}`}>
              <img className="accordionTitleIcon" src={section.iconTitle} />
              <h2 className="accordionTitle ">{section.title}</h2>
            </div>
            <p
              className={`accordionArrow ${
                expandedAccordion === section.section ? "open" : "notOpen"
              }`}
            >
              ►
            </p>
          </motion.header>
          <motion.section
            className={`accordionContentSection `}
            initial={{ height: 0, padding: "0px 24px" }}
            animate={{
              height: expandedAccordion === section.section ? "auto" : 0,
              padding:
                expandedAccordion === section.section ? "24px" : "0px 24px",
            }}
            transition={{ duration: 0.7, ease: "easeInOut" }}
          >
            {Array.isArray(section.desc) ? (
              section.desc.map((el, indx) =>
                el.startsWith("/", 0) ? (
                  <div
                    className={`accordionContentImage ${
                      indx % 2 === 0 ? "left" : "right"
                    }`}
                    style={{
                      background: `no-repeat url("${el}")`,
                    }}
                  ></div>
                ) : (
                  <div className="accordionContentDesc">
                    <p className="accordionContentDescText">{el}</p>
                  </div>
                )
              )
            ) : Object.keys(section.desc)[0].startsWith("section") ? (
              <div className="accordionContentSubSection">
                {Object.values(section.desc).map((subSection) => (
                  <>
                    <motion.header
                      className="accordionSubHeader"
                      initial={{ background: "#cc9753" }}
                      animate={{
                        background:
                          expandedTeam === subSection[1]
                            ? "#083063"
                            : "#cc9753",
                      }}
                      transition={{ duration: 0.7, ease: "easeInOut" }}
                      onClick={() => handleChangeTeam(subSection[1])}
                    >
                      <h2 className="accordionTitle ">{subSection[0]}</h2>
                      <p
                        className={`accordionArrow ${
                          expandedTeam === subSection[1] ? "open" : "notOpen"
                        }`}
                      >
                        ►
                      </p>
                    </motion.header>
                    <motion.section
                      className={`accordionContentSubSectionDesc `}
                      initial={{ maxHeight: 0 }}
                      animate={{
                        maxHeight: expandedTeam === subSection[1] ? "65rem" : 0,
                      }}
                      transition={{ duration: 0.7, ease: "easeInOut" }}
                    >
                      {subSection[2].map((card) => (
                        <Card el={card} />
                      ))}
                    </motion.section>
                  </>
                ))}
              </div>
            ) : (
              ""
            )}
          </motion.section>
        </div>
      ))}
    </>
  );
};
export default AccordionAbout;
